.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.Cross, .Close {
  opacity: 0.2; }
  .Cross:hover, .Close:hover {
    opacity: 0.3; }
  .Cross:before, .Close:before, .Cross:after, .Close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 100%;
    width: 2px;
    background-color: #333; }
  .Cross:before, .Close:before {
    transform: rotate(45deg); }
  .Cross:after, .Close:after {
    transform: rotate(-45deg); }

.MessageBoard {
  position: relative;
  height: 100%;
  background: rgba(215, 215, 215, 0.05);
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.15);
  border-radius: 3px;
  padding: 8px 24px 8px 20px; }
  .MessageBoard .Heading {
    position: relative;
    padding: 10px 0;
    font-family: "Montserrat", sans-serif;
    font-size: 13px;
    font-weight: 700;
    text-align: center;
    letter-spacing: 0.54px;
    text-transform: uppercase; }
  .MessageBoard--Info .Heading {
    color: rgba(26, 188, 156, 0.68); }
  .MessageBoard--Warning .Heading {
    color: rgba(240, 115, 113, 0.68); }
  .MessageBoard ul {
    padding-left: 0;
    list-style-type: none; }
    .MessageBoard ul li {
      display: flex;
      align-items: center;
      margin-bottom: 8px;
      letter-spacing: 0.58px; }

.Circle {
  display: inline-flex;
  width: 14px;
  height: 14px;
  line-height: 1;
  border: 1px solid transparent;
  border-radius: 50%;
  font-family: "Montserrat", sans-serif;
  font-size: 8px;
  font-weight: 500;
  justify-content: center;
  align-items: center;
  margin-right: 24px;
  border-color: darkgray; }
  .Circle .Number {
    color: darkgray;
    position: relative;
    left: 0.5px; }
  .Circle .FilledCircle {
    display: inline-flex;
    width: 8px;
    height: 8px;
    line-height: 1;
    border: 1px solid transparent;
    border-radius: 50%;
    font-family: "Montserrat", sans-serif;
    font-size: 8px;
    font-weight: 500;
    justify-content: center;
    align-items: center;
    background-color: rgba(240, 115, 113, 0.68); }
  .Circle--Red {
    border-color: rgba(240, 115, 113, 0.68); }

.Text {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 700;
  color: #4F5362;
  width: calc(100% - 40px); }

.Close {
  position: absolute;
  height: 10px;
  width: 10px;
  right: 10px;
  cursor: pointer; }



