.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.recipientAddress {
  max-width: 100px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis; }

.frame {
  align-self: flex-start; }

.table td, .table th {
  text-align: right; }

.table td {
  font-weight: 500; }

.table th:first-child,
.table td:first-child {
  flex-basis: 70px;
  text-align: right; }

.table td:first-child {
  font-weight: 500;
  color: #b1b1b3;
  font-size: 12px;
  line-height: 20.4px; }

.table th:nth-child(2),
.table td:nth-child(2) {
  flex-basis: 40px;
  padding-left: 0;
  padding-right: 0;
  text-align: center;
  text-transform: uppercase; }

.table th:nth-child(3),
.table td:nth-child(3) {
  flex-basis: 100px;
  flex-grow: 0;
  text-align: left;
  height: 19px; }

.table th:nth-child(4),
.table td:nth-child(4) {
  flex-basis: 46px;
  text-align: center; }

.table th:nth-child(5),
.table td:nth-child(5) {
  flex-basis: 58px;
  text-align: right; }



