.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.StatusIndicator--Online:before {
  background-color: #1ABC9C; }

.StatusIndicator--Out-Of-Sync:before {
  background-color: #F5BD00; }

.StatusIndicator--Closed:before {
  background-color: #70737F; }

.StatusIndicator:before {
  content: "";
  width: 13px;
  height: 13px;
  border-radius: 20px;
  display: inline-block;
  margin-right: 5px;
  vertical-align: sub; }

.OasisStatus {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 500;
  padding-left: 20px;
  color: #4F5362;
  letter-spacing: 1.3px;
  text-transform: uppercase; }
  .OasisStatus .NetworkStatus {
    display: flex;
    margin-top: 5px;
    align-items: center;
    font-family: "Montserrat", sans-serif;
    font-size: 13px;
    font-weight: 500;
    color: rgba(79, 83, 98, 0.8); }

.OffersLoadProgress {
  position: relative;
  top: 4px;
  font-size: 11px; }



