.marketTable th:first-child,
.marketTable td:first-child {
  flex-basis: 148px;
  text-align: center; }

.marketTable th:nth-child(2),
.marketTable td:nth-child(2) {
  flex-basis: 78px;
  text-align: right; }

.marketTable th:nth-child(3),
.marketTable td:nth-child(3) {
  flex-basis: 123px;
  text-align: right; }

.pair {
  font-weight: 600;
  display: flex; }
  .pair span {
    width: 50%; }
  .pair span:first-child {
    text-align: right;
    margin-right: 5px; }
  .pair span:last-child {
    text-align: left;
    margin-left: 5px;
    color: rgba(74, 74, 74, 0.6); }

.createDaiBtn {
  float: right;
  margin-top: -1px; }



