.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.rowTabs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px 0; }
  .rowTabs:before, .rowTabs:after {
    content: '';
    width: calc((100% - 496px) / 2);
    border-top: 0.5px solid #a3a5ab;
    border-bottom: 0.5px solid #a3a5ab; }

.tabs {
  display: flex;
  width: 496px;
  border: 1px solid #a3a5ab;
  border-radius: 100px;
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 0;
  overflow: hidden; }

.tab {
  flex-basis: 100%;
  height: 40px;
  border-right: 1px solid #a3a5ab;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px; }
  .tab:hover {
    background: #E8E9ED;
    cursor: pointer; }
    .tab:hover a {
      color: #4F5362; }
  .tab:last-child {
    border-right: 0; }
  .tab a {
    color: #a3a5ab;
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    font-size: 12px;
    font-weight: 500;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .tab a:hover {
      color: #4F5362; }
  .tab.active {
    background-color: #E8E9ED; }
    .tab.active, .tab.active > a {
      color: #4F5362; }

.tabBig {
  flex-basis: 130%; }



