.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.OasisWidgetFrame {
  position: relative;
  display: inline-block;
  background: white;
  border: 1px solid #EFF1F3;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.13);
  border-radius: 4px;
  width: 442px;
  min-height: 305px;
  margin: 0 16px 36px 16px;
  padding: 20px 0 0;
  z-index: 0; }
  .OasisWidgetFrame .Heading {
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    font-weight: 400;
    text-align: left;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 18px; }

.OasisWidgetContent {
  padding: 0 30px; }

.WidgetFloatRightButton {
  float: right;
  position: relative;
  top: -1px; }

.OasisWidgetFooter {
  position: absolute;
  bottom: 30px;
  left: 30px;
  right: 30px; }



