.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1em;
  padding-bottom: 2em; }

.transferTable {
  margin-bottom: 1em; }
  .transferTable th,
  .transferTable .thHeader {
    width: 100px; }
  .transferTable .thCurrency {
    width: 70px; }
  .transferTable thead td {
    padding: 0;
    border: 0; }

.tdWithErrorMessages {
  overflow: visible;
  position: relative; }

.validationErrorsBox {
  display: flex;
  width: 100%;
  height: 34px; }



