.selectContainer {
  border: 1px solid #C9C9C9;
  background-color: transparent;
  display: inline-block; }

.select {
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: transparent;
  color: #68686B;
  border: none;
  z-index: 1;
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: normal; }
  .select:hover {
    cursor: pointer; }
  .select:hover[disabled] {
    cursor: default; }

.selectGlyph {
  color: #aaaebb;
  z-index: -1; }

.md {
  border-radius: 4px;
  font-size: 10px;
  padding: 0 8px 0 2px; }
  .md .select {
    margin-right: -20px;
    padding: 8px 30px 8px 10px;
    font-size: 12px; }



