.select {
  float: right;
  position: relative;
  top: -1px; }

.chartjsTooltip {
  opacity: 0;
  position: absolute;
  background: #FEFEFE;
  color: white;
  padding: 10px;
  border-radius: 4px;
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
  pointer-events: none;
  -webkit-transform: translate(-50%, 10px);
  transform: translate(-50%, 10px);
  min-width: 120px;
  width: auto;
  min-height: 75px;
  border: 1px solid rgba(208, 208, 208, 0.25);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1); }
  .chartjsTooltip :global(.row-custom-tooltip) {
    clear: left;
    height: 22px; }
  .chartjsTooltip :global(.row-custom-tooltip.middle) {
    border-top: 1px #F7F8F9 solid;
    border-bottom: 1px #F7F8F9 solid; }
  .chartjsTooltip :global(span.left), .chartjsTooltip :global(span.right) {
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    color: #4A4A4A;
    line-height: 22px;
    text-transform: uppercase; }
  .chartjsTooltip :global(span.left) {
    float: left;
    margin-right: 10px; }
  .chartjsTooltip :global(span.right) {
    float: right; }



