.box {
  border: 1px solid;
  border-radius: 8px;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  display: flex; }

.sm {
  padding: 7px 16px;
  font-size: 13px; }

.md {
  padding: 16px;
  font-size: 13px; }

.noBorder {
  border-width: 0;
  margin: 0;
  padding: 0; }

.fullWidth {
  width: 100%; }

.default {
  border-color: #E5E9EB;
  color: #939194; }

.muted {
  border-color: #E5E9EB;
  color: #d4d4d4; }

.danger {
  border-color: #EF5350;
  color: #EF5350; }

.success {
  border-color: #26A69A;
  color: #26A69A; }



