.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.available {
  display: inline-block;
  float: right; }

.available-amount {
  background: none;
  border: none;
  color: #68686C;
  font-family: 'Roboto', sans-serif;
  font-size: 19px;
  font-weight: 500;
  padding-right: 10px;
  text-align: right;
  display: inline-block; }

.available-currency {
  box-sizing: border-box;
  color: #68686c;
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 1px;
  padding: 5px 11px;
  text-align: left;
  text-transform: uppercase;
  width: 70px;
  vertical-align: middle;
  display: inline-block; }

.loading {
  float: right; }



