.icon {
  margin-right: 1em;
  margin-top: -2px; }

.baseText {
  font-weight: 400; }

.bolderText {
  font-weight: 500;
  white-space: nowrap; }

.buying {
  margin-bottom: 0.75em; }

.detailsAmountCol {
  flex-basis: 45%; }

.detailsTradingCol {
  flex-basis: 65%;
  align-items: baseline; }

.detailsTradingFirstCol {
  flex-basis: 55%;
  flex-grow: 1; }

.tradingStatus {
  border-radius: 6px;
  display: inline-flex;
  height: 20px;
  width: 92px;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  top: -1px; }

.active {
  border: 1px solid #19B092;
  color: #26A69A; }

.disabled {
  border: 1px solid #EF5350;
  color: #EF5350; }



