.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

thead {
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  font-weight: 700;
  line-height: 2;
  text-transform: uppercase;
  opacity: 0.68;
  color: #222228;
  background-color: #F8F7F5; }

tbody {
  font-family: 'Roboto', sans-serif; }

tr.clickable {
  cursor: pointer;
  border-top: 1px transparent solid;
  border-bottom: 1px transparent solid; }

tr.clickable:hover {
  border-bottom: 1px #DCDDE0 solid;
  border-top: 1px #DCDDE0 solid;
  background: rgba(248, 247, 245, 0.15); }

tr.active {
  background: rgba(248, 247, 245, 0.5);
  border-top: 1px #E8E9EA solid;
  border-bottom: 1px #E8E9EA solid;
  cursor: pointer; }

td, th {
  padding: 5px 11px;
  margin: 0;
  flex-basis: 0;
  flex-grow: 0;
  flex-shrink: 0;
  overflow: hidden;
  box-sizing: content-box; }

table.scrolling tr {
  display: flex; }

table.scrolling th,
table.scrolling td {
  max-width: 100%;
  display: block;
  padding: 0;
  margin: 5px 11px; }

table.scrolling tbody {
  overflow-x: hidden;
  overflow-y: auto;
  display: block;
  margin-top: 10px;
  height: 160px;
  width: 98%; }

tr.collapseRow {
  border-bottom: 1px #DCDDE0 solid;
  border-top: 1px #DCDDE0 solid;
  margin-bottom: 1px; }
  tr.collapseRow > .collapseCell {
    font-size: 12px;
    color: #666669;
    text-transform: uppercase; }
    tr.collapseRow > .collapseCell:first-child:last-child {
      flex-grow: 1;
      flex-basis: 100%;
      text-align: center;
      font-weight: 500; }

.scrolling tr.emptyFallback {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .scrolling tr.emptyFallback > td:first-child:last-child {
    flex-grow: 1;
    flex-basis: 100%;
    text-align: center;
    font-weight: 500; }



