.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.OasisFooter {
  margin-top: 16px;
  padding: 40px 80px;
  justify-content: space-evenly; }

.LinksSection {
  display: flex;
  flex-direction: column; }
  .LinksSection .Heading {
    margin: 16px 0;
    font-family: "Montserrat", sans-serif;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    color: #4A4A4A; }
  .LinksSection .Link {
    margin: 16px 0;
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    font-weight: 700;
    color: #546979;
    cursor: pointer; }
    .LinksSection .Link:hover, .LinksSection .Link:active, .LinksSection .Link:focus {
      text-decoration: none; }



