.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

table.table td, table.table th {
  text-align: right;
  margin-left: 8px;
  margin-right: 8px; }

table.table td {
  font-weight: 500; }

table.table th:first-child,
table.table td:first-child {
  flex-basis: 72px;
  text-align: center; }

table.table td:first-child {
  font-weight: 500;
  color: #b1b1b3;
  font-size: 12px;
  line-height: 20.4px; }

table.table th:nth-child(2),
table.table td:nth-child(2) {
  flex-basis: 41px;
  text-align: center; }

table.table th:nth-child(3),
table.table td:nth-child(3) {
  flex-basis: 82px;
  text-align: right; }

table.table th:nth-child(4),
table.table td:nth-child(4) {
  flex-basis: 82px;
  text-align: right; }

table.table th:nth-child(5),
table.table td:nth-child(5) {
  flex-basis: 55px;
  text-align: right; }



