.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.FooterSeparator {
  margin-top: 50px;
  margin-bottom: 0;
  border-top: 1px solid #eee; }

.appIsLoading, .appIsLoading * {
  cursor: progress !important; }

.globalFormLockEnabled, .globalFormLockEnabled *:not(input) {
  cursor: wait !important; }



