.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.available {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  margin-bottom: 16px;
  text-transform: uppercase;
  font-size: 15px; }

.callToAction {
  float: right;
  min-width: 100px; }

.footer {
  display: flex;
  margin-top: 17px;
  align-items: center; }

.helpBlock {
  flex-grow: 1;
  font-weight: 500;
  color: #b1b1b3;
  font-size: 12px;
  line-height: 20.4px; }



